import React from 'react';

const User = (props) => {

	const { min, username, avatar } = props

	return (
		<a href="/" className={ min ? "user user--min" : "user" }>
			<div className="user__img"
				style={{ backgroundImage: `url(${avatar})`}}>
			</div>
			<div className="user__name">
				{username}
			</div>
		</a>
	);
}

export default User;