import React, { Component } from 'react';

import Picture from './Picture';
import User from './User';

import { withSwapi } from './hoc-helpers';
import Loader from './Loader';


class FeedPage extends Component {

	state = {
		users: [],
		loading: true
	}

	componentDidMount() {
		this.updateUsers()
	}

	updateUsers = () => {
		this.setState( {
			loading: true
		});

		this.props.getData()
			.then((users) => {
				this.setState({
					users,
					loading: false
				})
			})
	}

	render() {

		const { users, loading } = this.state;
			
		if (loading) {
			return <Loader />
		}

		const posts = users.map((item) => {

			const { id, name, avatar } = item
			
			return(
				<article key={id} className="post">
					<User username={name} avatar={avatar}/>
					<Picture></Picture>
				</article>
			)
		})

		return (
			<section className="feed">
				<div className="feed__inner inner">
					<div className="feed__main">
						{ posts }
					</div>
					<aside className="feed__aside"></aside>
				</div>
			</section>
		);
	}
}

const mapMethodsToProps = (swapi) => {
	return {
		getData: swapi.getAllPeople,
		getImageUrl: swapi.getPersonImg
	}
};

export default withSwapi(mapMethodsToProps)(FeedPage)